import reactCarouselStyles from 'react-multi-carousel/lib/styles.css'

import type { LinksFunction, MetaFunction } from '@remix-run/node'
import {
  Links,
  LiveReload,
  Meta,
  Outlet,
  Scripts,
  ScrollRestoration,
} from '@remix-run/react'
import { CacheProvider, Theme, ThemeProvider } from '@emotion/react'
import { CssBaseline } from '@mui/material'
import { createEmotionCache } from 'css-theme/emotion-cache'

import theme from '../css-theme/theme'
import { ReactNode } from 'react'

export const meta: MetaFunction = () => ({
  charset: 'utf-8',
  title: 'Ball Hog',
  viewport: 'width=device-width,initial-scale=1',
})

export const links: LinksFunction = () => {
  return [
    {
      rel: 'stylesheet',
      href: 'https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap',
    },
    {
      rel: 'stylesheet',
      href: reactCarouselStyles,
    },
  ]
}

const cache = createEmotionCache()

export interface DocumentProps {
  children: ReactNode
  theme: Theme
}

export default function App() {
  return (
    <Document theme={theme}>
      <CssBaseline />
      <Outlet />
    </Document>
  )
}

const Document = ({ children, theme }: DocumentProps) => {
  return (
    <html lang="en">
      <head>
        <Meta />
        <Links />
        <style id="emotion" suppressHydrationWarning />
      </head>
      <CacheProvider value={cache}>
        <ThemeProvider theme={theme}>
          <body data-theme={theme}>
            {children}
            <ScrollRestoration />
            <Scripts />
            <LiveReload />
          </body>
        </ThemeProvider>
      </CacheProvider>
    </html>
  )
}
